import "../styles/Navbar.css";
import SearchIcon from "@mui/icons-material/Search";
import MenuIcon from "@mui/icons-material/Menu";

function Navbar() {
  const burgerClickHandler = () => {
    const dropdown = document.getElementsByClassName("navbar__dropdown");
    if (dropdown[0].style.display === "none") {
      console.log("1");
      dropdown[0].style.display = "flex";
    } else {
      dropdown[0].style.display = "none";
      window.addEventListener("resize", () => {
        dropdown[0].style.display = "none";
      });
    }
  };

  return (
    <>
      <div className="navbar">
        <div className="navbar__logo">
          <h2>BookPod.</h2>
        </div>
        <div className="navbar__links">
          <div>Home</div>
          <div>Categories</div>
          <div>Podcasts</div>
        </div>
        <div className="navbar__search">
          <SearchIcon className="navbar__search__icon" />
          <input type="text" placeholder="Search..." />
        </div>
      </div>
      <div className="navbar-mob">
        <MenuIcon className="navbar-mob__burger" onClick={burgerClickHandler} />
        <h2>BookPod.</h2>
        <SearchIcon className="navbar-mob__search" />
      </div>
      <div className="navbar__dropdown" style={{ display: "none" }}>
        <div className="dropdown__links">Home</div>
        <div className="dropdown__links">Categories</div>
        <div className="dropdown__links">Podcast</div>
      </div>
    </>
  );
}

export default Navbar;
